.paper {
  background-color: rgb(4, 99, 128, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 380px;
  width: 100%;
  height: 400px;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input {
  width: 250px;
  margin-top: 20px;
  height: 30px;
  border: 2px solid rgba(4, 99, 128, 0.4);
  border-radius: 5px;
}

.input:focus {
  border-color: rgba(4, 99, 128, 0.9);
  outline: none;
}

.checkbox {
  width: 20px;
  height: 20px;
  margin: 0;
}

.text {
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.button {
  margin-top: 20px;
  max-width: 257px;
  width: 100%;
  height: 40px;
  color: white;
  background-color: #046380;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 500;
  line-height: 1.75;
  letter-spacing: 0.02857em;
  box-shadow: 0 3px 1px -2px rgb(0 0 0 / 20%), 0 2px 2px 0 rgb(0 0 0 / 14%),
    0 1px 5px 0 rgb(0 0 0 / 12%);
}

.button:hover {
  background-color: #043953;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.button:active {
  background-color: rgb(4, 99, 128, 0.9);
}

.checkboxText {
  display: flex;
  justify-content: flex-start;
  margin-top: 20px;
  width: 256px;
}
.commonError {
  padding: 5px;
  color: red;
}
.credentials {
  margin-top: 32px;
  max-width: 380px;
  width: 100%;
}
.credentials h4 {
  margin-bottom: 12px;
}
